.header{
  max-width: 1910px;
  margin: 0 auto;
  padding: 20px 20px;
}
.main{
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 20px;
}
.main-wrapper,
.main-title{
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 (@offset * 2);
}

/* container */
.header,
.main{
  position: relative;
}

.header::after,
.main::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}