/* flex-box */
@font-face {
  font-family: 'OpenSans-Regular';
  src: url("../fonts/OpenSans-Regular.woff2") format('woff2'), url("../fonts/OpenSans-Regular.woff") format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'OpenSans-SemiBold';
  src: url("../fonts/OpenSans-SemiBold.woff2") format('woff2'), url("../fonts/OpenSans-SemiBold.woff") format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'OpenSans-Bold';
  src: url("../fonts/OpenSans-Bold.woff2") format('woff2'), url("../fonts/OpenSans-Bold.woff") format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.header {
  max-width: 1910px;
  margin: 0 auto;
  padding: 20px 20px;
}
.main {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 20px;
}
.main-wrapper,
.main-title {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 108px;
}
/* container */
.header,
.main {
  position: relative;
}
.header::after,
.main::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.icon {
  fill: #fff;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.icon-star {
  fill: #efecca;
  width: 19px;
  height: 18px;
  flex-shrink: 0;
}
.icon-star:hover,
.icon-star:focus,
.icon-star:active {
  fill: #a7a37e;
  cursor: pointer;
}
.icon-arrow {
  fill: #046380;
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 4px;
  flex-shrink: 0;
}
.icon-arrow:hover {
  cursor: pointer;
}
.icon-up {
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.header {
  background-color: #002f2f;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  color: #fefefe;
}
.menu-list {
  display: flex;
  font-size: 24px;
  padding-left: 20px;
}
.menu-list__link {
  margin-right: 54px;
}
.menu-list__link:hover,
.menu-list__link:active {
  color: #a7a37e;
}
.header-phone {
  display: flex;
  flex-direction: column;
}
.header-phone__link {
  font-size: 32px;
}
.header-phone__link:hover,
.header-phone__link:active {
  color: #a7a37e;
}
.header-phone__text {
  font-style: normal;
  font-size: 20px;
}
.burger-btn {
  display: none;
  padding: 10px 15px;
  margin-top: 15px;
}
.burger-btn:hover {
  cursor: pointer;
}
.burger-btn:hover .icon {
  fill: #a7a37e;
}
.view {
  display: block !important;
  transition: 3s;
}
@media screen and (max-width: 1285px) {
  .header-logo {
    padding: 0 10px;
  }
  .menu-list {
    margin-left: 10px;
    font-size: 20px;
  }
  .menu-list__link {
    margin-right: 30px;
  }
}
@media screen and (max-width: 1064px) {
  .header {
    display: flex;
  }
  .header-logo,
  .header-phone {
    order: 1;
  }
  .header-menu {
    order: 2;
    padding: 20px 0;
  }
  .header__link {
    font-size: 28px;
  }
  .menu-list {
    margin-left: 0;
  }
}
@media screen and (max-width: 780px) {
  .header {
    padding: 20px 10px;
  }
  .menu-list {
    font-size: 18px;
  }
  .menu-list__link {
    margin-right: 25px;
  }
  .header-phone__link {
    font-size: 28px;
  }
  .header-phone__text {
    font-size: 18px;
  }
}
@media screen and (max-width: 680px) {
  .header {
    flex-wrap: nowrap;
    position: relative;
  }
  .menu-list {
    display: none;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    top: 80px;
    right: 0;
    border-radius: 3px;
    background: #002f2f;
    transition: 3s;
    z-index: 555;
  }
  .menu-list__item {
    padding: 10px 10px;
  }
  .menu-list__link {
    margin-right: 20px;
  }
  .burger-btn {
    display: block;
    order: 1;
    padding: 0;
    margin-top: 0;
  }
}
@media screen and (max-width: 420px) {
  .menu-list {
    font-size: 16px;
    top: 90px;
  }
  .menu-list__link {
    margin-right: 10px;
  }
  .header-phone__link {
    font-size: 24px;
  }
  .header-phone__text {
    font-size: 16px;
  }
}
.main {
  background-color: #efecca;
  padding-bottom: 100px;
}
.main-title {
  font-family: 'OpenSans-Bold';
  font-size: 48px;
  color: #002f2f;
  padding-top: 60px;
  padding-bottom: 60px;
}
.main-wrapper {
  background-color: #fff;
}
.main-sorting {
  display: flex;
  color: rgba(0, 47, 47, 0.702);
  padding-top: 28px;
  padding-bottom: 18px;
}
.main-sorting__price,
.main-sorting__rooms {
  display: flex;
  align-items: center;
}
.main-sorting__price {
  margin-left: 10px;
}
.main-sorting__rooms {
  margin-left: 18px;
}
.icon-wrapp {
  margin-left: 8px;
  padding: 0 6px;
  border-radius: 50%;
  background-color: #7ba6bb26;
}
.main-items {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -27px;
}
.main-items__item {
  box-sizing: border-box;
  margin: 27px;
  width: calc(100% / 3 - 54px);
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 4.4%;
  border: 1px solid #efecca;
  border-radius: 3px;
}
.main-items__btn {
  display: flex;
  margin: 0 auto;
  text-transform: uppercase;
  font-family: 'OpenSans-SemiBold';
  color: #fff;
  margin-top: 27px;
  margin-bottom: 143px;
  width: 45%;
  background-color: #046380;
  border-radius: 3px;
}
.main-items__btn .btn-show {
  text-align: center;
  padding: 29px;
  width: 100%;
}
.item-image,
.item-wrapper {
  display: flex;
  margin-top: auto;
}
.item-wrapper {
  flex-direction: column;
  margin-top: auto;
  padding-left: 54px;
  padding-right: 54px;
}
.item-title {
  font-family: 'OpenSans-Bold';
  font-size: 24px;
  color: #002f2f;
  padding-top: 30px;
  padding-bottom: 20px;
}
.item-info {
  display: flex;
  padding-top: 30px;
  padding-bottom: 40px;
}
.item-info__value {
  font-size: 13px;
  margin-right: 8px;
}
.item-value {
  font-family: 'OpenSans-SemiBold';
  font-size: 24px;
  padding-bottom: 25px;
}
.item-offer {
  position: absolute;
  display: flex;
  top: 16px;
  left: 19px;
}
.item-offer__action {
  text-transform: uppercase;
  font-size: 12px;
  color: #fff;
  padding: 4px;
  border-radius: 3px;
  background-color: #ff4444;
}
.item-offer__action:not(:last-child) {
  margin-right: 19px;
}
.item-offer .weight {
  font-family: 'OpenSans-SemiBold';
}
.item-button {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  font-family: 'OpenSans-SemiBold';
  font-size: 12px;
  color: #fff;
  text-align: center;
  padding: 13px 0;
  width: 100%;
}
.item-icon {
  position: absolute;
  top: 16px;
  right: 16px;
}
.finish {
  position: relative;
  font-size: 16px;
  color: #046380;
}
.finish::before {
  position: absolute;
  content: '';
  width: 3px;
  height: 100%;
  left: -5px;
  background-color: #046380;
}
.value-size {
  font-size: 18px;
}
.booked {
  background-color: #a7a37e;
}
.free {
  background-color: #002f2f;
}
.sales {
  background-color: #a1a1a1;
}
.bord-dark {
  border-color: #002f2f;
}
.bord-light {
  border-color: #a1a1a1;
}
.main-offer {
  background-color: #046380;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  padding: 50px 0;
  border-radius: 3px;
}
.main-offer__title {
  display: flex;
  flex-direction: column;
  font-family: 'OpenSans-SemiBold';
  font-size: 34px;
}
.main-offer__title .title-text {
  font-family: 'OpenSans-Regular';
  font-size: 16px;
}
.main-offer__info {
  display: flex;
  align-items: end;
}
.main-offer__info .info-email {
  display: flex;
  flex-direction: column;
  margin-right: 52px;
}
.main-offer__info .info-email__input {
  padding: 16px 19px;
}
.main-offer__info .info-email__container {
  display: flex;
  align-items: center;
  padding-top: 18px;
}
.main-offer__info .info-email__container .container-text {
  text-transform: uppercase;
  font-size: 18px;
  font-style: normal;
}
.main-offer__info .info-email__btn {
  display: flex;
  text-transform: uppercase;
  padding: 16px 44px 14px 44px;
  border-radius: 3px;
  background-color: #a7a37e;
}
.main-offer__info .info-email__btn:hover,
.main-offer__info .info-email__btn:active {
  cursor: pointer;
  background-color: #a1a1a1;
}
.info-email__container {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  user-select: none;
}
.info-email__container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkmark {
  position: absolute;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
}
.info-email__container input:checked ~ .checkmark {
  background-color: #fff;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.info-email__container input:checked ~ .checkmark:after {
  display: block;
}
.info-email__container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid #046380;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
@media screen and (max-width: 1200px) {
  .main-wrapper,
  .main-title {
    padding: 0 54px;
  }
  .main-title {
    font-size: 40px;
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media screen and (max-width: 1040px) {
  .item-title {
    font-size: 20px;
  }
}
@media screen and (max-width: 950px) {
  .main-items__btn {
    margin-top: 22px;
    margin-bottom: 110px;
  }
  .main-offer {
    padding: 35px 10px;
  }
  .main-offer__title {
    font-size: 30px;
  }
  .main-offer .title-text {
    font-size: 15px;
  }
  .main-offer .info-email {
    margin-right: 40px;
  }
  .main-offer .info-email__input {
    padding: 14px 16px;
  }
  .main-offer .info-email__container {
    padding-top: 14px;
  }
  .main-offer .info-email__container .checkmark {
    height: 18px;
    width: 18px;
  }
  .main-offer .info-email__container .container-text {
    font-size: 16px;
  }
  .main-offer .info-email__btn {
    padding: 14px 35px 13px 35px;
  }
  .info-email__container .checkmark:after {
    left: 7px;
    top: 3px;
  }
}
@media screen and (max-width: 890px) {
  .main-items__item {
    width: calc(100% / 2 - 54px);
  }
}
@media screen and (max-width: 820px) {
  .main-title {
    font-size: 34px;
    padding-top: 35px;
    padding-bottom: 35px;
  }
  .main-sorting {
    padding-top: 20px;
    padding-bottom: 5px;
  }
  .main-items__btn {
    margin-top: 18px;
    margin-bottom: 80px;
  }
  .main-items__btn .btn-show {
    padding: 22px;
  }
  .main-offer {
    justify-content: center;
    flex-direction: column;
    padding: 25px 10px;
  }
  .main-offer__title {
    font-size: 28px;
    padding-bottom: 20px;
  }
  .main-offer .title-text {
    font-size: 14px;
  }
  .info-email__container {
    padding-top: 10px;
  }
  .checkmark {
    height: 16px;
    width: 16px;
  }
  .container-text {
    font-size: 14px;
  }
}
@media screen and (max-width: 660px) {
  .main-wrapper,
  .main-title {
    padding: 0 108px;
  }
  .main-title {
    font-size: 26px;
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .main-sorting {
    padding-top: 15px;
    padding-bottom: 0;
  }
  .main-items__item {
    width: calc(100% / 1 - 54px);
  }
  .main-items__btn {
    margin-top: 12px;
    margin-bottom: 60px;
    width: 66%;
  }
  .main-items__btn .btn-show {
    padding: 20px;
  }
}
@media screen and (max-width: 600px) {
  .main-wrapper,
  .main-title {
    padding: 0 54px;
  }
  .main-title {
    font-size: 24px;
    padding-top: 22px;
    padding-bottom: 22px;
  }
}
@media screen and (max-width: 490px) {
  .main-sorting {
    font-size: 12px;
  }
  .main-sorting__price {
    margin-left: 5px;
  }
  .main-sorting__rooms {
    margin-left: 8px;
  }
  .main-sorting .icon-wrapp {
    margin-left: 4px;
  }
  .main-items__item {
