.icon{
  fill: #fff;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.icon-star{
  fill: #efecca;
  width: 19px;
  height: 18px;
  flex-shrink: 0;
}
.icon-star:hover,
.icon-star:focus,
.icon-star:active{
  fill: @color_btn_back_booked;
  cursor: pointer;
}
.icon-arrow{
  fill: @color_icon_ar;
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 4px;
  flex-shrink: 0;
}
.icon-arrow:hover{
  cursor: pointer;
}
// scroll up
.icon-up{
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}