      //btn
@color_btn_back_booked: #a7a37e;
@color_btn_back_free: #002f2f;
@color_btn_back_sales: #a1a1a1;

      /* flex-box */
@offset: 54px;
@offset_one_side: (@offset / 2);

.row-flex(){
  display: flex;
  flex-wrap: wrap;
  margin: 0 -@offset_one_side;
}

.col(){
  box-sizing: border-box;
  margin: @offset_one_side;
}

.size(@n){
  width: calc(100% / @n - @offset);
}