.scrollup{
  display: flex;
  align-items: center;
  padding: 29px;
  opacity: .6;
  border-radius: 50%;
  background: @color_btn_back_booked;
  position:fixed;
  bottom:10%;
  right:5%;
  display:none;
}


//media
@media screen and (max-width: 1200px){

  .scrollup{
    padding: 24px;
    right: 1%;
  }
}

@media screen and (max-width: 1040px){

  .scrollup{
    padding: 20px;
  }
}

@media screen and (max-width: 820px){

  .scrollup{
    padding: 16px;
  }
}

@media screen and (max-width: 600px){

  .scrollup{
    padding: 14px;
  }
}

@media screen and (max-width: 340px){

  .scrollup{
    padding: 12px;
  }
}