.main{
  background-color: @color_main_back;
  padding-bottom: 100px;

  &-title{
    font-family: 'OpenSans-Bold';
    font-size: 48px;
    color: @color_font_title;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &-wrapper{
    background-color: #fff;
  }
  &-sorting{
    display: flex;
    color: rgba(0, 47, 47, 0.702);
    padding-top: 28px;
    padding-bottom: 18px;

    &__price,
    &__rooms{
      display: flex;
      align-items: center;
    }
    &__price{
      margin-left: 10px;
    }
    &__rooms{
      margin-left: 18px;
    }
  }
}
.icon-wrapp{
  margin-left: 8px;
  padding: 0 6px;
  border-radius: 50%;
  background-color: @color_icon_ar_back;
}
.main-items{
  .row-flex();

  &__item{
    .col();
    .size(3);
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 4.4%;
    border: 1px solid @color_main_back;
    border-radius: 3px;
  }
  &__btn{
    display: flex;
    margin: 0 auto;
    text-transform: uppercase;
    font-family: 'OpenSans-SemiBold';
    color: #fff;
    margin-top: 27px;
    margin-bottom: 143px;
    width: 45%;
    background-color: @color_text_item;
    border-radius: 3px;

    .btn-show{
      text-align: center;
      padding: 29px;
      width: 100%;
    }
  }
}
.item{
  &-image,
  &-wrapper{
    display: flex;
    margin-top: auto;
  }
  &-wrapper{
    flex-direction: column;
    margin-top: auto;
    padding-left: @offset;
    padding-right: @offset;
  }
  &-title{
    font-family: 'OpenSans-Bold';
    font-size: 24px;
    color: @color_font_title;
    padding-top: 30px;
    padding-bottom: 20px;
  }
  &-info{
    display: flex;
    padding-top: 30px;
    padding-bottom: 40px;

    &__value{
      font-size: 13px;
      margin-right: 8px;
    }
  }
  //price-value
  &-value{
    font-family: 'OpenSans-SemiBold';
    font-size: 24px;
    padding-bottom: 25px;
  }
  // action
  &-offer{
    position: absolute;
    display: flex;
    top: 16px;
    left: 19px;

    &__action{
      text-transform: uppercase;
      font-size: 12px;
      color: #fff;
      padding: 4px;
      border-radius: 3px;
      background-color: @color_action_back;
    }
    &__action:not(:last-child){
      margin-right: 19px;
    }
    .weight{
      font-family: 'OpenSans-SemiBold';
    }
  }
  &-button{
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    font-family: 'OpenSans-SemiBold';
    font-size: 12px;
    color: #fff;
    text-align: center;
    padding: 13px 0;
    width: 100%;
  }
  &-icon{
    position: absolute;
    top: 16px;
    right: 16px;
  }
}
.finish{
  position: relative;
  font-size: 16px;
  color: @color_text_item;
}
.finish::before{
  position: absolute;
  content: '';
  width: 3px;
  height: 100%;
  left: -5px;
  background-color: @color_text_item;
}
.value-size{
  font-size: 18px;
}
// btn back
.booked{
  background-color: @color_btn_back_booked;
}
.free{
  background-color: @color_btn_back_free;
}
.sales{
  background-color: @color_btn_back_sales;
}
// border
.bord-dark{
  border-color: @color_btn_back_free;
}
.bord-light{
  border-color: @color_btn_back_sales;
}

// main-offer
.main-offer{
  background-color: @color_text_item;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  padding: 50px 0;
  border-radius: 3px;

  &__title{
    display: flex;
    flex-direction: column;
    font-family: 'OpenSans-SemiBold';
    font-size: 34px;

    .title-text{
      font-family: 'OpenSans-Regular';
      font-size: 16px;
    }
  }
  &__info{
    display: flex;
    align-items: end;

    .info-email{
      display: flex;
      flex-direction: column;
      margin-right: 52px;

      &__input{
        padding: 16px 19px;
      }
      &__container{
        display: flex;
        align-items: center;
        padding-top: 18px;

        .container-text{
          text-transform: uppercase;
          font-size: 18px;
          font-style: normal;
        }
      }
      &__btn{
        display: flex;
        text-transform: uppercase;
        padding: 16px 44px 14px 44px;
        border-radius: 3px;
        background-color: @color_btn_back_booked;

      }
      &__btn:hover,
      &__btn:active{
        cursor: pointer;
        background-color: @color_btn_back_sales;
      }
    }
  }
}
// checkbox
.info-email__container{
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  user-select: none;
}
.info-email__container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;;
}
.checkmark {
  position: absolute;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
}
.info-email__container input:checked ~ .checkmark {
  background-color: #fff;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.info-email__container input:checked ~ .checkmark:after {
  display: block;
}
.info-email__container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid @color_text_item;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}



//media
@media screen and (max-width: 1200px){

  .main-wrapper,
  .main-title{
    padding: 0 @offset;
  }
  .main-title{
    font-size: 40px;
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 1040px){

  .item-title{
    font-size: 20px;
  }
}

@media screen and (max-width: 950px){

  @offset: 44px;

  .main-items__btn{
    margin-top: 22px;
    margin-bottom: 110px;
  }
  .main-offer{
    padding: 35px 10px;

    &__title{
      font-size: 30px;
    }
    .title-text{
      font-size: 15px;
    }
    .info-email{
      margin-right: 40px;

      &__input{
        padding: 14px 16px;
      }
      &__container{
        padding-top: 14px;

        .checkmark{
          height: 18px;
          width: 18px;
        }
        .container-text{
          font-size: 16px;
        }
      }
      &__btn{
        padding: 14px 35px 13px 35px;
      }
    }
  }
  .info-email__container .checkmark:after {
    left: 7px;
    top: 3px;
  }
}

@media screen and (max-width: 890px){

  .main-items__item{
    .size(2);
  }
}

@media screen and (max-width: 820px){

  .main-title {
    font-size: 34px;
    padding-top: 35px;
    padding-bottom: 35px;
  }
  .main-sorting {
    padding-top: 20px;
    padding-bottom: 5px;
  }
  .main-items__btn {
    margin-top: 18px;
    margin-bottom: 80px;

    .btn-show{
      padding: 22px;
    }
  }
  // offer
  .main-offer{
    justify-content: center;
    flex-direction: column;
    padding: 25px 10px;

    &__title{
      font-size: 28px;
      padding-bottom: 20px;
    }
    .title-text {
      font-size: 14px;
    }
  }
  .info-email__container {
    padding-top: 10px;
  }
  .checkmark{
    height: 16px;
    width: 16px;
  }
  .container-text{
    font-size: 14px;
  }
}

@media screen and (max-width: 660px){

  .main-wrapper,
  .main-title{
    padding: 0 (@offset * 2);
  }
  .main-title {
    font-size: 26px;
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .main-sorting {
    padding-top: 15px;
    padding-bottom: 0;
  }
  .main-items__item{
    .size(1);
  }
  .main-items__btn {
    margin-top: 12px;
    margin-bottom: 60px;
    width: 66%;

    .btn-show{
      padding: 20px;
    }
  }
}

@media screen and (max-width: 600px){

  .main-wrapper,
  .main-title{
    padding: 0 @offset;
  }
  .main-title {
    font-size: 24px;
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

@media screen and (max-width: 490px){

  .main-sorting{
    font-size: 12px;

    &__price {
      margin-left: 5px;
    }
    &__rooms {
      margin-left: 8px;
    }
    .icon-wrapp {
      margin-left: 4px;
    }
  }
  .main-items__item{
    padding-top: 12%;
  }
  .main-offer__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .main-offer .info-email {
    margin-right: 10px;
    padding-bottom: 20px;
  }
  .main-offer .info-email__container .container-text {
    font-size: 12px;
  }
}

@media screen and (max-width: 440px){

  @offset: 40px;
  .main-wrapper,
  .main-title{
    padding: 0 (@offset / 2);
  }
  .main-title {
    font-size: 22px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media screen and (max-width: 340px){

  .main-title {
    font-size: 18px;
  }
  .main-sorting{
    font-size: 11px;

    &__price {
      margin-left: 3px;
    }
    &__rooms {
      margin-left: 4px;
    }
    .icon-wrapp {
      margin-left: 3px;
    }
  }
  .main-offer__title {
    font-size: 25px;
  }
  .main-offer .title-text{
    font-size: 12px;
  }
}