.header{
  background-color: @color_back;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  color: @color_font_menu;
}
.menu-list{
  display: flex;
  font-size: 24px;
  padding-left: 20px;

  &__link{
    margin-right: @offset;
  }
  &__link:hover,
  &__link:active{
    color: @color_btn_back_booked;
  }
}
.header-phone{
  display: flex;
  flex-direction: column;

  &__link{
    font-size: 32px;
  }
  &__link:hover,
  &__link:active{
    color: @color_btn_back_booked;
  }
  &__text{
    font-style: normal;
    font-size: 20px;
  }
}
// burger
.burger-btn{
  display: none;
  padding: 10px 15px;
  margin-top: 15px;
}
.burger-btn:hover{
  cursor: pointer;
  .icon{
    fill: @color_btn_back_booked;
  }
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}



// media
@media screen and (max-width: 1285px) {

  .header-logo{
    padding: 0 10px;
  }
  .menu-list{
    margin-left: 10px;
    font-size: 20px;

    &__link{
      margin-right: 30px;
    }
  }
}

@media screen and (max-width: 1064px){

  .header {
    display: flex;

    &-logo,
    &-phone {
      order: 1;
    }

    &-menu {
      order: 2;
      padding: 20px 0;
    }
    &__link{
      font-size: 28px;
    }
  }
  .menu-list {
    margin-left: 0;
  }
}

@media screen and (max-width: 780px){

  .header{
    padding: 20px 10px;
  }
  .menu-list{
    font-size: 18px;

    &__link{
      margin-right: 25px;
    }
  }
  .header-phone__link{
    font-size: 28px;
  }
  .header-phone__text{
    font-size: 18px;
  }
}

@media screen and (max-width: 680px){

  .header{
    flex-wrap: nowrap;
    position: relative;
  }
  .menu-list{
    display: none;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    top: 80px;
    right: 0;
    border-radius: 3px;
    background: @color_back;
    transition: 3s;
    z-index: 555;

    &__item{
      padding: 10px 10px;
    }
    &__link{
      margin-right: 20px;
    }
  }
  .burger-btn{
    display: block;
    order: 1;
    padding: 0;
    margin-top: 0;
  }
}

@media screen and (max-width: 420px){

  .menu-list {
    font-size: 16px;
    top: 90px;

    &__link{
      margin-right: 10px;
    }
  }
  .header-phone__link{
    font-size: 24px;
  }
  .header-phone__text{
    font-size: 16px;
  }
}
